import React from 'react'
import './Home.css'
import KeyWords from '../Components/KeyWords'

export default class Home extends React.Component{
    render(){
        return(
            <div className="home">
                <div className="lefth">
                    <h1><div className="gray">Hi!</div></h1>
                    <h1>
                        <div className="iam">
                            <div className="gray">I'm </div><KeyWords/>
                        </div>
                    </h1>
                    <p className="description">I am a junior majoring in Computer Science at The University of Texas at El Paso.<br/><br/>
                    I work as an Instructional Assistant for fundamental Computer Science classes at UTEP helping students understand and reach a solution to their CS problems.<br/><br/>
                    I love solving puzzles and doing crafts which is why I can take on any challenge and find practical solutions to any problem! </p>
                </div>
                <div className="righth">
                    <img src="ElisaJTodd.jpg" height ="100%" alt="Elisa in Toledo"/>
                </div>
            </div>
        )
    }
}
