import React from 'react'
import './Resume.css'
import { FaDownload } from 'react-icons/fa';
import { FaFilePdf } from 'react-icons/fa';
import { FaFileWord } from 'react-icons/fa';

export default class Resume extends React.Component{
    render(){
        console.log("resume");
        return(
            <div className="section">
                <div className="left">
                    <embed src="Resume.pdf" width="100%" height="100%" />
                </div>
                <div className="right">
                    <FaDownload className="main-icon"/><br/>
                    <h1>Resume</h1>
                    <a href="resume.pdf" download="ElisaJTodd_Resume">
                        <button>
                            <div>Download PDF</div>
                            <FaFilePdf className="icon"/>
                        </button>
                    </a>
                    <a href="resume.docx" download="ElisaJTodd_Resume">
                        <button>
                            <div>Download docx</div>
                            <FaFileWord className="icon "/>
                        </button>
                    </a>
                    <h1>Transcript</h1>
                    <a href="Transcript.docx" download="ElisaJTodd_Transcript">
                        <button>
                            <div>Download PDF</div>
                            <FaFileWord className="icon "/>
                        </button>
                    </a>
                </div>
            </div>
        )
    }
}
