import React from 'react';
import NavigationBar from './Components/NavigationBar'
import Home from './Pages/Home'
import AboutMe from './Pages/AboutMe'
import Resume from './Pages/Resume'
import './App.css';

export enum Pages{
  Home = "Home",
  AboutMe = "About Me",
  Resume = "Resume"
}

class App extends React.Component <{}, {activePage: Pages}>{
  constructor(props:any) {
    super(props);
    this.state = { activePage: Pages.Home };
  }
  render(){
    return (
      <div className="App">
        <header className="App-header">
          <NavigationBar activePage={this.state.activePage} changePage={(page:Pages) => this.changePage(page)}/>
        </header>
        <div className="App-content">
        {this.getPage()}
        </div>
      </div>
    );
  }
  private getPage(){
    switch(this.state.activePage){
      case Pages.Home:
        return <Home/>;
      case Pages.AboutMe:
        return <AboutMe/>;
      case Pages.Resume:
        return <Resume/>;
    }
  }
  private changePage(nextPage:Pages){
    this.setState({activePage: nextPage})
  }
}

export default App;
