import React from 'react'
import { Pages } from '../App'
import './PageBar.css'

interface PageBarProps{
    activePage:Pages;
    changePage: (page:Pages) => void;
}

export default class PageBar extends React.Component <PageBarProps>{
    render(){
        const pageNames = Object.values(Pages);
        return(
            <div className="line">
                {pageNames.map((page)=> this.lineForActive(page))}
            </div>
        )
    }
    private lineForActive(page:Pages){
            let active = null;
            let style = "default";
            if(this.props.activePage === page){
                active = <hr/>;
                style = "purple";
            }
            return(
                <div className={style} key={page} onClick={() => this.props.changePage(page)}> 
                    {page}
                    {active}
                </div>
            );
        }
}
